import React, { Component } from 'react';
import axios from 'axios';
import Hotel from '../components/Hotel';

// import hotels from './hotels';
// const hotelsList = hotels.records;

class Home extends React.Component {
    state = {
        isLoading: true,
        hotels:[]
    }
    getHotels = async () => {
        const {
            data: {
                data: {hotels}
            }
        } = await axios.get("./hotels.json");
        this.setState({ hotels, isLoading: false});
    };
    componentDidMount() {
        this.getHotels();
    }
    render(){
        const { isLoading, hotels } = this.state;
        
        return (
            <section className="container">
                {
                    isLoading ? 
                    <div>Loading...</div> :
                    <div>Wow</div> 
                }
            </section>
        );
    }
}


export default Home;